<template>
  <div class="students container">
    <h1 class="pageheader">
      Manage Students
    </h1>
    <Alert v-if="alert" v-bind:message="alert " />
    <div class="table-responsive">
      <table class="table table-stripped">
        <thead>
          <tr>
            <th>Name</th>
            <th>Registration Number</th>
            <th>Faculty</th>
            <th>Department</th>
            <th>Level</th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="student in students" v-bind:key="student.id">
            <td>{{student.name}}</td>
            <td>{{student.RegNumber}}</td>
            <td>{{student.Faculty}}</td>
            <td>{{student.Dept}}</td>
            <td>{{student.Level}}</td>            
            <td><router-link class="btn btn-success" v-bind:to="'/edit/'+student.id">Edit</router-link></td>
            <td><button class="btn btn-danger" v-on:click="deleteStudent(student.id)">Delete</button></td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
  import Alert from './Alert';
  export default {
    name: 'students',
    data () {
      return {
        students: [],
        alert:''
      }
    },
    methods: {
      fetchStudents(){
        this.$http.get('https://studentmgt-app.herokuapp.com/api/students')
          .then(function(response){
            console.log(response.body);
            this.students = response.body;
          });
      },
      deleteStudent(id){
        this.$http.delete('https://studentmgt-app.herokuapp.com/api/students/delete/:id'+id)
          .then(function(response){
            this.$router.push({path: '/students', query: {alert: 'Student Deleted'}});
          });
      }
    },
    created: function() {
      if (this.$route.query.alert) {
        this.alert = this.$route.query.alert;
      }
      this.fetchStudents();
    },
    updated: function() {
      this.fetchStudents();
    },
    components: {
      Alert
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



