<template>
  <div class="edit container">
    <h1 class="page-header">
        Edit Student
    </h1>
    <form v-on:submit="updateStudent" class="form-horizontal">
        <div class="form-group">
            <label for="inputEmail3" class="col-sm-2 control-label">Full Name</label>
            <div class="col-sm-4">
            <input type="text" class="form-control" id="inputEmail3" placeholder="Full Name" v-model="student.name" required>
            </div>
        </div>
        
        <div class="form-group">
            <label for="inputPassword3" class="col-sm-2 control-label">Registration Number</label>
            <div class="col-sm-4">
            <input type="text" class="form-control" id="inputPassword3" placeholder="Reg Number" v-model="student.RegNumber" required>
            </div>
        </div>
        
        <div class="form-group">
            <label for="inputPassword3" class="col-sm-2 control-label">Faculty</label>
            <div class="col-sm-4">
            <input type="text" class="form-control" id="inputPassword3" placeholder="Faculty" v-model="student.Faculty" required>
            </div>
        </div>

        <div class="form-group">
            <label for="inputPassword3" class="col-sm-2 control-label">Department</label>
            <div class="col-sm-4">
            <input type="text" class="form-control" id="inputPassword3" placeholder="Dept" v-model="student.Dept" required>
            </div>
        </div>

        <div class="form-group">
            <label for="inputPassword3" class="col-sm-2 control-label">Level</label>
            <div class="col-sm-4">
            <input type="text" class="form-control" id="inputPassword3" placeholder="Level" v-model="student.Level" required>
            </div>
        </div>
        
        <div class="form-group">
            <div class="col-sm-offset-2 col-sm-10">
            <button type="submit" class="btn btn-default">Sign in</button>
            </div>
        </div>

    </form>
  </div>
</template>

<script>
export default {
  name: 'add',
  data () {
    return {
      student:{}
    }
  },
  methods: {
      fetchStudent(id){
          this.$http.get('https://studentmgt-app.herokuapp.com/api/students/:id')
          .then(function(response){
              this.student = response.body;
          });
      },
      updateStudent(e){
          if (!this.student.name || !this.student.RegNumber || !this.student.Faculty || !this.student.Dept || !this.student.Level) {
              console.log('Please fill in all required fields');
          } else {
              let updateStudent = {
                  name: this.student.name,
                  RegNumber: this.student.RegNumber,
                  Faculty: this.student.Faculty,
                  Dept: this.student.Dept,
                  Level: this.student.Level
              }

              this.$http.post('https://studentmgt-app.herokuapp.com/api/students/update/:id'+this.$routes.params.id, updateStudent)
                .then(function(response){
                    this.$router.push({path: '/students', query:{alert: 'Student Updated'}});
                })
              e.preventDefault();
          }
          console.log(123);
          e.preventDefault();
      }
  },
  created: function(){
      this.fetchStudent(this.$)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



